import React, {Component} from "react";
import {OraSelect, SwitchButtons} from "eshop/components/OraCommonComponents";
import ClientContextCheckbox from "./ClientContextCheckbox";
import PropTypes from "prop-types";
import SalesChannel from "../../core/enum/SalesChannel";

const propTypes = {
    selectedProcessValue: PropTypes.string,
    processTypeData: PropTypes.arrayOf(PropTypes.shape({
        value: PropTypes.string,
        description: PropTypes.string
    })),
    selectProcessCallback: PropTypes.func,
    selectedLoyaltyLengthValue: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]),
    loyaltyLengthData: PropTypes.arrayOf(PropTypes.shape({
        value:  PropTypes.oneOfType([
                       PropTypes.string,
                       PropTypes.number
                   ]),
        description: PropTypes.string
    })),
    selectLoyaltyLengthCallback: PropTypes.func
}

//TODO trzeba by jakos ladnie odseparowac ten komponent od logiki zeby byl reuzywalny, ale czy na pewno jest nam on potrzebny reuzywalny :P
//TODO ClientContextCheckbox jest przypięty do redux store, selectorów i reducerów - nie będzie reużywalny poza tym kontekstem -
//TODO myślę, że to jest OK, bo nawet jeśli będzie reużywany, to w ramach tej samej aplikacji z tym samym storem.
export default class OfferFilterComponents extends Component {

    render() {
        let selectWidthClass = this.props.switchType === "TAB" && this.props.showSwitch ? "l-col-6" : "l-col-4";
        return (
            <div className={"l-page-row u-small-box-shadow" + (this.props.switchType === "TAB" ? " u-large-padding-top-l u-medium-padding-l u-small-padding-l" : " u-padding-all-l")}>
                {
                    (this.props.headerText) && <h1>{this.props.headerText}</h1>
                }
                <div className="l-row">
                    {(this.props.switchType === "TAB" && this.props.showSwitch) &&<div className="l-col l-col-4 l-col-medium-12 l-col-small-12">
                        <SwitchButtons options={this.props.switchConf} onClick={this.props.onClickSwitch} switchType={this.props.switchType}/>
                    </div>}
                    <div className={(this.props.switchType === "TAB" && this.props.showSwitch) ? "l-col l-col-small-12 l-col-medium-12 l-col-8" : ""}>
                        <div className={"l-col l-col-small-12 l-col-medium-12" + ((this.props.switchType === "TAB") ? " l-col-8" : " l-col-5")}>
                            <div className="l-row u-medium-padding-top-l u-small-padding-top-l">
                                <div className={"l-col l-col-small-12 l-col-medium-6 u-small-padding-l u-medium-no-padding-left " + (this.props.processTypeData.length > 1  ? "" : "u-padding-top-m ") + selectWidthClass}>
                                    <OraSelect
                                        options={this.props.processTypeData }
                                        id="processTypeFilter"
                                        selected={this.props.selectedProcessValue}
                                        onChange={this.props.selectProcessCallback}
                                        className="u-small-left u-left u-w-100 u-small-w-auto u-medium-w-100 u-small-w-100"
                                        isFeedbackParam="true"
                                        selectClassName="opl-input--size-m"
                                        />
                                </div>
                                <div className={"l-col l-col-small-12 l-col-medium-6 " + selectWidthClass}>
                                    <OraSelect
                                        options={this.props.loyaltyLengthData}
                                        id="loyaltyFilter"
                                        selected={this.props.selectedLoyaltyLengthValue}
                                        onChange={this.props.selectLoyaltyLengthCallback}
                                        className="u-small-left u-left u-w-100 u-small-w-auto u-medium-w-100 u-small-w-100"
                                        selectClassName="opl-input--size-m"
                                        singleOptionClassName="u-small-no-padding-left"
                                        />
                                </div>
                            </div>
                        </div>

                        <div className={"l-col l-col-small-12 l-col-medium-12" + ((this.props.switchType === "TAB") ? " l-col-4" : " l-col-7")}>
                            <div className="l-row u-medium-padding-top-l">
                                {(this.props.channels.sales===SalesChannel.WWW) &&
                                <div className={"l-col l-col-small-12 " + ((this.props.switchType === "TAB") ? "l-col-medium-4 l-col-12": "l-col-medium-6 l-col-5") +  " u-padding-top u-small-padding-top-l"}>
                                    <ClientContextCheckbox {...this.props.clientContextCheckboxConfig} channels={this.props.channels}
                                         alignClass=" u-small-left u-large-right u-small-no-padding-left"/>
                                </div>}
                                {(this.props.showSwitch && this.props.switchType !== "TAB") &&<div className="l-col l-col-small-12 l-col-medium-6 l-col-7  u-small-padding-top-l">
                                    <SwitchButtons options={this.props.switchConf} onClick={this.props.onClickSwitch} switchType={this.props.switchType}/>
                                </div>}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

OfferFilterComponents.propTypes = propTypes;
